import React, { useCallback } from 'react';
import { Inline, RenderNodeProps } from '@ali/4ever-cangjie';
import Formula from './content';
import { FormulaConfig } from '../types';

interface WrapperProps extends RenderNodeProps<Inline> {
  config?: FormulaConfig;
}

export default function (props: WrapperProps) {
  const stopPropagation = useCallback((e: React.SyntheticEvent) => {
    e.stopPropagation();
  }, []);

  return (
    <div
      onClick={stopPropagation}
      onMouseDown={stopPropagation}
      onKeyDown={stopPropagation}
      onMouseUp={stopPropagation}
      onTouchStart={stopPropagation}
      {...props.attributes}
    >
      <Formula {...props} />
    </div>
  );
}
